<template>
  <div class="modal" @click="closeModal">
    <div class="modal__box" @click.stop="">
      <h3 class="modal__box-title">Параметры профиля</h3>
      <div class="modal__box-inputs">
        <label>
          <span>Ваше имя</span>
          <input
            type="text"
            class="modal__box-input"
            placeholder="Ваше имя"
            v-model.trim="name"
          />
        </label>
        <label>
          <span>URL фото профиля</span>
          <input
            type="text"
            class="modal__box-input"
            placeholder="URL"
            v-model="photo"
          />
        </label>
        <label>
          <span>URL background</span>
          <input
            type="text"
            class="modal__box-input"
            placeholder="URL"
            v-model="background"
          />
        </label>
        <label>
          Цвет приложения
          <input type="color" v-model="panelColor" />
        </label>
      </div>
      <div class="modal__box-btns">
        <button class="modal__box-btn back" @click="closeModal">Отмена</button>
        <button class="modal__box-btn send" @click="change">ОТПРАВИТЬ</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      photo: "",
      background: "",
      panelColor: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal");
      this.name = this.photo = this.background = this.panelColor = "";
    },
    change() {
      const changeProfile = {
        name: this.name ? this.name : "DefaultName",
        photo: this.photo
          ? this.photo
          : "https://thumbs.dreamstime.com/b/%D1%87%D0%B5%D0%BB%D0%BE%D0%B2%D0%B5%D0%BA-%D1%85%D0%B8%D0%BF%D1%81%D1%82%D0%B5%D1%80-%D0%B0%D0%B2%D0%B0%D1%82%D0%B0%D1%80-%D0%BC%D1%83%D0%BB%D1%8C%D1%82%D1%8F%D1%88%D0%BD%D1%8B%D0%B9-%D0%BF%D0%B0%D1%80%D0%B5%D0%BD%D1%8C-%D1%81-%D1%87%D0%B5%D1%80%D0%BD%D1%8B%D0%BC%D0%B8-%D0%B2%D0%BE%D0%BB%D0%BE%D1%81%D0%B0%D0%BC%D0%B8-%D0%BF%D0%BB%D0%BE%D1%81%D0%BA%D0%B8%D0%B9-223717055.jpg",
        panelColor: this.panelColor ? this.panelColor : "#323232",
        background: this.background
          ? this.background
          : "https://i.pinimg.com/736x/3d/8c/2f/3d8c2f2c82c1c9ef1e27be645cd1aa17.jpg",
      };
      this.$emit("changeProfile", changeProfile);
      this.closeModal();
    },
  },
};
</script>

