<template>
  <div class="modal" @click="closeModal">
    <div class="modal__box" @click.stop="">
      <h3 class="modal__box-title">Отправить картинку</h3>
      <div class="modal__box-inputs">
        <label>
          <span>URL</span>
          <input
            type="text"
            class="modal__box-input"
            placeholder="URL"
            v-model.trim="url"
          />
        </label>
        <label>
          <span>Комментарий</span>
          <textarea
            class="modal__box-input"
            placeholder="Комментарий"
            v-model.trim="comment"
          />
        </label>
      </div>
      <div class="modal__box-btns">
        <button class="modal__box-btn back" @click="closeModal">Отмена</button>
        <button class="modal__box-btn send" @click="sendPhoto">
          ОТПРАВИТЬ
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      url: "",
      comment: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal");
      this.url = this.comment = "";
    },
    sendPhoto() {
      if (this.url != "" && this.comment != "") {
        const messagePhoto = {
          url: this.url,
          comment: this.comment,
        };
        this.$emit("sendPhoto", messagePhoto);
        this.closeModal();
      }
    },
  },
};
</script>

