<template>
  <div class="message" :class="getClass">
    <div
      class="message__text"
      :class="getClass + '__Text'"
      v-if="message.text"
      @click="$emit('delMessage', message.id)"
    >
      {{ message.text }}
    </div>

    <div
      class="message__photo"
      :class="getClass + '__Text'"
      v-else
      @click="$emit('delMessage', message.id)"
    >
      <img
        :src="message.url"
        alt=""
        class="message__photo-img"
      />
      <h3 class="message__photo-text">{{ message.comment }}</h3>
    </div>

    <div class="message__date">
      {{ message.date }}
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: {
    message: Object,
    screen: Number,
  },
  methods: {},
  computed: {
    getClass() {
      if (this.screen == 1) {
        return this.message.sender;
      }
      return this.message.sender == "person1" ? "person2" : "person1";
    },
  },
};
</script>

