<template>
  <div class="box">
    <Person1
      :allMessage="allMessage"
      @send="addNewMessage"
      @openModal="openModal"
      :screen="1"
      @typing1="typing1"
      :friendTyping="nowTyping2"
      :userInfo="user1"
      @changeProfile="openChangeModal"
      @delMessage="delMessage"
      @showPhoto="showPhoto"
    />
    <Person2
      :allMessage="allMessage"
      @send="addNewMessage"
      @openModal="openModal"
      :screen="2"
      @typing2="typing2"
      :friendTyping="nowTyping1"
      :userInfo="user2"
      @changeProfile="openChangeModal"
      @delMessage="delMessage"
      @showPhoto="showPhoto"
    />
    <Modal
      v-show="isModalOpen"
      @closeModal="isModalOpen = false"
      @sendPhoto="addNewMessage"
    />
    <ModalChangeProfile
      v-show="isChangeProfileOpen"
      @closeModal="isChangeProfileOpen = false"
      @changeProfile="changeProfile"
    />

    <div class="showPhoto" v-show="isShowPhoto.isActive">
      <img :src="isShowPhoto.url" alt="" />
      <button class="showPhoto__btn" @click="isShowPhoto.isActive = false">
        X
      </button>
    </div>

    <div class="showUpdate" v-show="showUpdate">
      <h3>Дополнительные возможности</h3>
      <div class="showUpdate__text">
        <p>Удаление сообщений при нажатии на сообщение</p>
        <p>Увелечение картинки при нажатии на неё</p>
        <p>Изменения профиля</p>
      </div>
      <button class="showUpdate__btn" @click="showUpdate = false">X</button>
    </div>
  </div>
</template>

<script>
import Person1 from "./components/Person1.vue";
import Person2 from "./components/Person2.vue";
import Modal from "./components/Modal.vue";
import ModalChangeProfile from "./components/ModalChangeProfile.vue";
export default {
  data() {
    return {
      isModalOpen: false,
      isChangeProfileOpen: false,
      screenChange: 1,
      nowSender: "",
      startId: 0,
      allMessage: [],
      nowTyping1: false,
      nowTyping2: false,
      isShowPhoto: {
        isActive: false,
        url: "",
      },
      showUpdate: true,
      user1: {
        name: "Name1",
        photo:
          "https://thumbs.dreamstime.com/b/%D1%87%D0%B5%D0%BB%D0%BE%D0%B2%D0%B5%D0%BA-%D1%85%D0%B8%D0%BF%D1%81%D1%82%D0%B5%D1%80-%D0%B0%D0%B2%D0%B0%D1%82%D0%B0%D1%80-%D0%BC%D1%83%D0%BB%D1%8C%D1%82%D1%8F%D1%88%D0%BD%D1%8B%D0%B9-%D0%BF%D0%B0%D1%80%D0%B5%D0%BD%D1%8C-%D1%81-%D1%87%D0%B5%D1%80%D0%BD%D1%8B%D0%BC%D0%B8-%D0%B2%D0%BE%D0%BB%D0%BE%D1%81%D0%B0%D0%BC%D0%B8-%D0%BF%D0%BB%D0%BE%D1%81%D0%BA%D0%B8%D0%B9-223717055.jpg",
        panelColor: "#323232",
        background:
          "https://i.pinimg.com/736x/3d/8c/2f/3d8c2f2c82c1c9ef1e27be645cd1aa17.jpg",
      },
      user2: {
        name: "Name2",
        photo:
          "https://thumbs.dreamstime.com/b/man-hipster-avatar-cartoon-guy-black-hair-red-glasses-flat-icon-blue-background-user-person-character-vector-151711777.jpg",
        panelColor: "#323232",
        background:
          "https://i.pinimg.com/736x/3d/8c/2f/3d8c2f2c82c1c9ef1e27be645cd1aa17.jpg",
      },
    };
  },
  components: {
    Person1,
    Person2,
    Modal,
    ModalChangeProfile,
  },
  methods: {
    addNewMessage(message) {
      const newMessage = message;
      newMessage.id = this.startId++;
      newMessage.date =
        (new Date().getHours() < 10
          ? "0" + new Date().getHours().toString()
          : new Date().getHours().toString()) +
        ":" +
        (new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes().toString()
          : new Date().getMinutes().toString());
      if (!newMessage.sender) {
        newMessage.sender = this.nowSender;
      }
      this.allMessage.push(newMessage);
    },
    openModal(sender) {
      this.isModalOpen = true;
      this.nowSender = sender;
    },
    typing1(nowTyping) {
      this.nowTyping1 = nowTyping;
    },
    typing2(nowTyping) {
      this.nowTyping2 = nowTyping;
    },
    changeProfile(profile) {
      if (this.screenChange == 1) {
        this.user1.name = profile.name;
        this.user1.photo = profile.photo;
        this.user1.panelColor = profile.panelColor;
        this.user1.background = profile.background;
      } else {
        this.user2.name = profile.name;
        this.user2.photo = profile.photo;
        this.user2.panelColor = profile.panelColor;
        this.user2.background = profile.background;
      }
    },
    openChangeModal(screen) {
      this.isChangeProfileOpen = true;
      this.screenChange = screen;
    },
    delMessage(id) {
      this.allMessage = this.allMessage.filter((message) => message.id != id);
    },
    getMessages() {
      if (localStorage.message) {
        this.allMessage = JSON.parse(localStorage.message);
      }
    },
    getId() {
      if (localStorage.id) {
        this.startId = JSON.parse(localStorage.id);
      }
    },
    showPhoto(url) {
      this.isShowPhoto.isActive = true;
      this.isShowPhoto.url = url;
    },
  },
  watch: {
    allMessage: {
      handler() {
        localStorage.message = JSON.stringify(this.allMessage);
      },
      deep: true,
    },
    startId: {
      handler() {
        localStorage.id = JSON.stringify(this.startId);
      },
      deep: true,
    },
  },
  mounted() {
    this.getMessages();
    this.getId();
  },
};
</script>

