<template>
  <div class="screen">
    <div class="screen__header" :style="{ background: userInfo.panelColor }">
      <img :src="userInfo.photo" alt="" class="screen__header-img" />
      <div class="screen__header-info">
        <div class="screen__header-left">
          <h2 class="screen__header-name">{{ userInfo.name }}</h2>
          <span class="screen__header-status">{{
            friendTyping ? "печатает..." : "Онлайн"
          }}</span>
        </div>
        <button class="screen__header-btn">
          <img
            src="https://www.freeiconspng.com/uploads/settings-icon-2.png"
            class="screen__header-btn-image"
            @click="this.$emit('changeProfile', screen)"
          />
        </button>
      </div>
    </div>
    <div
      class="screen__main"
      :style="{ background: 'url(' + userInfo.background + ')' }"
    >
      <div class="screen__main-notMessage" v-show="!allMessage.length">
        <span>Здесь пока ничего нет...</span>
      </div>
      <Message
        v-for="message in allMessage"
        :key="message.id"
        :message="message"
        :screen="screen"
        @delMessage="$emit('delMessage', message.id)"
        @showPhoto="$emit('showPhoto', message.url)"
      />
    </div>
    <div class="screen__footer">
      <button
        class="screen__footer-btn"
        v-show="!currectlyText"
        @click="sendPhoto"
      >
        <img
          src="@/assets/images/sendPhoto.png"
          alt=""
          class="screen__footer-img"
        />
      </button>
      <button
        class="screen__footer-btn"
        v-show="currectlyText"
        @click="sendMessage"
      >
        <img src="@/assets/images/send.png" alt="" class="screen__footer-img" />
      </button>

      <input
        type="text"
        class="screen__footer-input"
        name="input"
        placeholder="Написать сообщение..."
        v-model="inputText"
        :style="{ background: userInfo.panelColor }"
      />
    </div>
  </div>
</template>

<script>
import Message from "./Message.vue";
import Modal from "./Modal.vue";

export default {
  data() {
    return {
      inputText: "",
    };
  },
  methods: {
    sendMessage() {
      const message = {
        text: this.inputText.trim(),
        sender: "person" + this.screen,
      };
      this.$emit("send", message);
      this.inputText = "";
    },
    sendPhoto() {
      this.$emit("openModal", "person" + this.screen);
    },
  },
  components: {
    Message,
    Modal,
  },
  props: {
    allMessage: Array,
    screen: Number,
    sender: String,
    friendTyping: Boolean,
    userInfo: Object,
  },
  computed: {
    currectlyText() {
      this.$emit("typing1", Boolean(this.inputText.trim()));
      return this.inputText.trim();
    },
  },
};
</script>

